/* .{
    transform: translate();
} */
@keyframes animateUp {
  0% {
    top: -5px;
  }
  /* 25%  {background-color:yellow; left:200px; top:0px;}
    50%  {background-color:blue; left:200px; top:200px;}
    75%  {background-color:green; left:0px; top:200px;} */
  50% {
    top: 10px;
  }
  100% {
    top: -5px;
  }
}

@keyframes animateDown {
    0% {
      top: 5px;
    }
    /* 25%  {background-color:yellow; left:200px; top:0px;}
      50%  {background-color:blue; left:200px; top:200px;}
      75%  {background-color:green; left:0px; top:200px;} */
    50% {
      top: -10px;
    }
    100% {
      top: 5px;
    }
  }

/* The element to apply the animation to */
.animationup {
  position: relative;
  /* animation-name: example;
    animation-duration: 2s; */
  animation: infinite animateUp 4s ease-in-out;
}

.animationdown {
    position: relative;
    animation: infinite animateDown 4s ease-in-out;
}
